import axios from 'axios';

export default {
  // state is always namespaced itself, but mutations, etc, is not.
  namespaced: true,
  // every state should have a initial value;
  state: {
    foo: 'robots-foo',
    cart: [],
    parts: null,
  },
  // all changes in the store should in mutations;
  mutations: {
    //  
    addRobotToCart(state, robot) {
      /* eslint no-alert:0 */
      /* eslint no-restricted-globals:0 */
      state.cart.push(robot);
    },
    // API 
    updateParts(state, parts) {
      /* eslint no-alert:0 */
      /* eslint no-restricted-globals:0 */
      state.parts = parts;
      // es , mutataion ,
      // error eslintrc.js, rules , 'no-param-reassign': 0,
    },
  },
  actions: {
    getParts({ commit }) {
      axios.get('/api/parts')
        .then(result => commit('updateParts', result.data))
        .catch(console.error);
    },
    addRobotToCart({ commit, state }, robot) {
      const cart = [...state.cart, robot];
      // return  promise  
      return axios.post('/api/cart', cart)
        .then(() => commit('addRobotToCart', robot));
    },
  },
  // Cart Sale Item Getters
  getters: {
    cartSaleItems(state) {
      return state.cart.filter(item => item.head.onSale);
    },
    foo(state) {
      return `robots-getter/${state.foo}`;
    },
  },
};
